import { createApp } from './app/app.js';


const { app, router, store } = createApp();
import { preloadData } from './app/store';
import Logger from './app/logger';
const logger = new Logger(); 


function beforeResolve(to, from, next) {
    const matched = router.getMatchedComponents(to)
    const prevMatched = router.getMatchedComponents(from)
 
    //       ,
    //     ,    
    let diffed = false
    const activated = matched.filter((c, i) => {
      return diffed || (diffed = (prevMatched[i] !== c))
    })
 
    if (!activated.length) {
      return next()
    }
 
    //      ,   
 
    Promise.all(activated.map(c => {
      if (c.storeRegister) {
          c.storeRegister({store}); 
      }
      if (c.asyncData) {
        return c.asyncData({ store, route: to })
      }
    })).then(() => {
 
      //   
 
      next()
    }).catch(next);
};


if (window.__INITIAL_STATE__) {

    router.onReady(() => {
        const matchedComponents = router.getMatchedComponents();
        matchedComponents.forEach(Component => {
            if (Component.storeRegister) {
                Component.storeRegister({store}); 
            }
            store.replaceState(window.__INITIAL_STATE__);
        }); 
        router.beforeResolve(beforeResolve);
        app.$mount('#app')
    });
} else {
    //        
    router.onReady(() => {
        //  beforeResolve       
        router.beforeResolve(beforeResolve);
        preloadData(store)
            .then(() => {
                //          
                const matchedComponents = router.getMatchedComponents();
                //      
                if (matchedComponents.length === 0) {
                    return reject({ code: 404 });
                }
                //   asyncData    
                return Promise.all(matchedComponents.map(Component => {
                    //      
                    if (Component.storeRegister) {
                        Component.storeRegister({store});
                    }
                    if (Component.asyncData) {
                        return Component.asyncData({
                            store, route: router.currentRoute
                        });
                    }
                }));              
            })
            .then(() => {
                app.$mount('#app');
            }) 
            .catch(err => {
                logger.error(err); 
            });
    });
}




